<template>
  <div>
    <div class="container">
    <h1>Tipo de Seguro</h1>
  </div>
    <sure-type-table
        :data="sureTypeListState"
        @DeleteSureType="DeleteSureType"
        @addSureType="addSureType"
        @updateSureType="updateSureType"
    />
  </div>
</template>

<script>

import SureTypeTable from "@/components/sureType/SureTypeTable";
import {mapActions, mapGetters} from "vuex";
export default {
  name: "SureType",
  components: {
    SureTypeTable
  },
  data () {
    return {
      jsonDataUpdate: {
        detail: "",
        id: 0,
        name: ""
      }
    }
  },
  created() {
    this.getSureTypeState()
  },
  computed: {
    ...mapGetters(['sureTypeListState'])
  },
  methods: {
    ...mapActions(['getSureTypeState', 'setSureTypenew', 'apiUpdateSureType', 'apiDeleteSureType']),

    addSureType( item) {
      this.setSureTypenew(item)
    },
    updateSureType( item) {
      this.jsonDataUpdate.detail = item.detail;
      this.jsonDataUpdate.id = item.id;
      this.jsonDataUpdate.name = item.name;      
     
      this.apiUpdateSureType(this.jsonDataUpdate)
    },
    DeleteSureType(id) {
      this.apiDeleteSureType(id)
    }
  }
}
</script>

